<template>
  <div class="order-item">
    <!--用户信息 uid  -->
    <div class="user">
      <div class="uid">{{order.uid}}</div>
    </div>

    <!--商品信息  gid gtitle gsize gcount  gtime -->
    <div class="good">
      <div>{{order.ogtitle}}({{order.gid}})</div>
      <div>商品大小：{{order.ogsize}}</div>
      <div>商品数量：{{order.ogcount}}</div>
      <div>{{getTime}}</div>
    </div>
    <!--订单信息    gstatus  gprice-->
    <div class="order">
      <div class="sta" :class="{active: isActive}">{{order.ogstatus}}</div>
      <div class="op">￥{{order.ogprice}}</div>
    </div>
  </div>
</template>

<script>
  import {formatDate} from 'commons/utils'
  export default {
    name: 'OrdersItem',
    props: {
      order: {
        type: Object,
        default() {
          return {}
        }
      }
    },
    computed: {
      //转换时间
      getTime() {
        let date = new Date(this.order.otime*1000);
         // console.log(date.toLocaleString().replace(new RegExp('/','g'),"-"))
         return formatDate(date)
      },
      isActive() {
        if(this.order.ogstatus == '未收货') {
          return false
        } else {
          return true
        }
      }
    }
  }
</script>

<style scoped>
  .order-item{
    display: flex;
  justify-content:space-around;
  border-bottom: 2px solid rgba(100, 100, 100, 0.2);
  padding: 5px 0px;
  font-size: 13px;
  }
  .uid{
    font-size: 16px;
    line-height: 60px;
  }
  .op{
    color: red;
  }
  .active{
    color: green;
    font-size: 14px;
  }
  .sta{
    margin-top: 10px;
    margin-bottom: 18px;
  }
</style>