<template>
  <div class="allorders">
    <nav-bar class="nav-barl">
      <div slot="left" class="nav-left" @click="backClick">
        <img src="~assets/img/back.svg" alt="">
      </div>
      <div slot="center" class="nav-titles">订单管理</div>
    </nav-bar>
    <!--搜索栏-->
    <div class="search">
      <div>
      <input type="text" v-model="u" name="uid" placeholder="用户id"> 
      </div>
      <div>
      <input type="text" v-model="g" name="gid" placeholder="商品id">
      </div>
      <div>
      <input type="date" v-model="o" name="otime" class="ot">
      </div>
      <div class="sea" @click="searchOrders">搜索</div>
    </div>
    <!--收货-->

    <scroll class="content" ref="scroll"  :probe-type="3" @pullingUp="loadMore" :pull-up-load="true">
      <orders-list :orders="allOrders"></orders-list>
    </scroll>
  </div>
</template>

<script>
  import NavBar from 'common/navbar/NavBar'
  import {selByOrders} from 'network/order'
 import Scroll from 'common/scroll/Scroll'
 import OrdersList from './childcomps/OrdersList'
  export default {
    name: 'AllOrders',
    components: {
      NavBar,
      Scroll,
      OrdersList
    },
    data () {
      return {
        u: '',
        g: '',
        o: '',
        //存放uid   gid   otime  ogstatus  
        uid: '',
        gid: '',
        otime: '',
        showOrders: {page: 0, list: []}
      }
    },
    computed: {
      allOrders() {
        return this.showOrders.list
      }
    },
    created () {
      //请求未收货的全部订单  20条   uid,gid,otim,ogprice(页码)
      this.selByOrders()
    },
    methods: {
      backClick() {
         this.$router.back()
      },
      searchOrders() {
        //赋值
        this.uid = this.u;
        this.gid = this.g;
        this.otime = this.o;
        //点击   清空数组
        this.showOrders = {page: 0, list: []}
        this.selByOrders();
      },
      selByOrders() {
        //将日期转成秒
        let os = this.otime + " 00:00:00";
        let starttime = os.replace(new RegExp("-","gm"),"/");
        var ot = (new Date(starttime)).getTime() / 1000;
        //请求订单的方法
        selByOrders(this.uid,this.gid,ot,this.showOrders.page + 1).then(res => {
          this.showOrders.list.push(...res);
          this.showOrders.page += 1;
          this.$refs.scroll.finishPullUp();
        })
      },
      loadMore() {
         //监听图片的上拉  调用方法增加数据
         this.selByOrders();
        //刷新scroll  
        this.$refs.scroll.refresh();
      } 
    }
  }
</script>

<style scoped>
  .allorders{
    height: 100vh;
  }
   .nav-barl{
    background-color: #ff8198;
    color: white;
  }
  .nav-left{
    margin-top: 5px;
  }
  .search{
    display: flex;
    justify-content:space-around;
    height: 42px;
    width: 98%;
    background-color:snow;
    border-bottom: 2px solid #ff8198;
    margin: auto;
  }
  input{
    height: 15px;
    width: 50px;
    border-radius: 5px;
    margin-top: 10px;
    display: block;
  }
  .ot{
    width: 100px;
    font-size: 10px;
  }
  .sea{
    width: 40px;
    height: 20px;
    background-color: forestgreen;
    text-align: center;
    font-size: 13px;
    line-height: 20px;
    border-radius: 10px;
    margin-top: 8px;
  }
  .content{
     height: calc(100% - 93px - 44px);
    overflow: hidden;
  }
</style>